
.ulsNavbars {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #338DFF;
}

.lisNavbars {
    cursor: pointer; cursor: hand;
    float: left;
}

.lisNavbars a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

.lisNavbars a:hover {
    background-color: #4172B0;
}



